<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <div class="w_200">
                    <el-input placeholder="请输入关键字" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                </div>
               <!--  <div class="m_l_20">
                    <el-button @click="search">查询</el-button>
                </div> -->
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="withdrawalsRecordUuid" label="打款流水号">
                    </el-table-column>
                    <el-table-column align="center" prop="userId" label="用户ID">
                    </el-table-column>
                    <el-table-column align="center" prop="userName" label="姓名">
                    </el-table-column>
                    <el-table-column align="center" prop="type" label="提现方式" :formatter="formatType">
                    </el-table-column>
                    <el-table-column align="center" prop="accountNumber" label="提现账户">
                    </el-table-column>
                    <el-table-column align="center" prop="money" label="金额">
                    </el-table-column>
                    <el-table-column align="center" prop="handleUuid" label="负责人ID">
                    </el-table-column>
                    <el-table-column align="center" prop="createName" label="操作人">
                    </el-table-column>
                    <el-table-column align="center" label="状态" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column align="center" label="提现时间" width="150" :formatter="getTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="280">
                        <template scope="scope">
                            <el-button size="small" type="info" @click="editItem(scope.$index, scope.row)" >查看</el-button>
                            <el-button :disabled="scope.row.status == 1 ? false : true"size="small" type="success" @click="changeAgree(scope.$index, scope.row)" >通过</el-button>
                            <el-button :disabled="scope.row.status == 1 ? false : true"size="small" type="warning" @click="changeReject(scope.$index, scope.row)" >驳回</el-button>
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>


        <el-dialog class="edit dialog-info" title="查看详情" :visible.sync="editDialog">
            <div class="info-content">
                <el-row>
                    <el-col :span="12">
                        提现账号：<span>{{eForm.accountNumber}}</span>
                    </el-col>
                    <el-col :span="12">
                        提现类型：<span>{{eForm.accountType == 1 ? '银行卡' : eForm.accountType == 2 ? '支付宝' : eForm.accountType == 3 ? '微信' : '未知'}}</span>
                    </el-col>
                    <el-col :span="12">
                        提现状态：<span>{{eForm.applyStatus == 1 ? '申请中' : eForm.applyStatus == 2 ? '已通过' : eForm.applyStatus == 3 ? '已驳回' : '未知'}}</span>
                    </el-col>
                    <el-col :span="12">
                        提现银行：<span>{{eForm.bank}}</span>
                    </el-col>
                    <el-col :span="12">
                        处理意见：<span>{{eForm.handleDsp}}</span>
                    </el-col>
                    <el-col :span="12">
                        处理时间：<span>{{formatTime(eForm)}}</span>
                    </el-col>
                    <el-col :span="12">
                        提现金额：<span>{{eForm.money}}</span>
                    </el-col>
                    <el-col :span="12">
                        银行卡开户人姓名：<span>{{eForm.openAccountName}}</span>
                    </el-col>
                </el-row>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="editDialog = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            editDialog: false, //弹出框-编辑
            // 编辑-表单
            eForm: {},
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        // 计算表格高度
        tableMaxHeight() {
            this.$store.commit('setTableMaxHeight', this.tableTop)
            return this.$store.state.tableMaxHeight
        }
    },
    methods: {
        // 搜索
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.handleTime && row.handleTime.time)
            },
            // 格式化时间
            getTime(row, column) {
                return this.$common.getNowTime(row.handleTime)
            },
            // 格式化提现方式
            formatType(row, column) {
                return row.type == 1 ? '银行卡' : row.type == 2 ? '支付宝' : row.type == 3 ? '微信' : '未知'
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.status == 1 ? '申请中' : row.status == 2 ? '已通过' : row.status == 3 ? '已驳回' : '未知'
            },
        // 查看-编辑
        editItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "id": row.id
                }
            }
            this.$common._post('withdrawalsRecord/detail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data.withdrawalsRecord)
            })
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                    "id": row.id
                }
            }
            this.$common.deleteItem(this.API + 'withdrawalsRecord/delete', this, json)
        },
        // 通过
        changeAgree(index, row) {
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                           "id": row.id,
                        }
                    }
                    this.$common._post('withdrawalsRecord/agree', json, this, data => {
                        this.$message({ type: 'success',message: data.description })
                        this.getList(this.word)
                    })
                }).catch(() => {})
        },
        // 驳回
        changeReject(index, row) {
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                           "id": row.id
                        }
                    }
                    this.$common._post('withdrawalsRecord/reject', json, this, data => {
                        this.$message({ type: 'success',message: data.description })
                        this.getList(this.word)
                    })
                }).catch(() => {})
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelWithdrawalsRecord', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(userId = ''){
            this.listLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "userId": userId,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('withdrawalsRecord/list', json, this, data => {
                this.total = data.total
                this.tableData = data.list
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



